	module.exports = {"data":{"currentPage":{"html":"<h1>Getting some Test Fixtures</h1>\n<p>To get our tests up and running, we need to setup some test fixtures. All Test fixtures really means is a <code>fixed state</code> used as a baseline for running tests in Software testing.</p>\n<p>In our particular use case, we need to consistently simulate a fixed return state for all the events our bot will be responding to.</p>\n<p>Again, the Probot and Github team shines here. This was really a breeze. There are two ways to get these fixtures for our events. One is directly on Github via the Advanced tab in the Bot settings page. The other is directly on SMEE. I went with the Github route, only because I think after our app is deployed and we have our own server recieving the webhooks, this is probably the route we will stay with. The SMEE route had the advantage of listing out the event name, making it easier to find the specific fixture you want, as seen below.</p>\n<h3>SMEE Recent Webhook Deliveries</h3>\n<p><img src=\"/images/probot-04-smee-deliveries.png\" alt=\"SMEE Recent Webhook Deliveries\"></p>\n<h3>Github Recent Webhook Deliveries</h3>\n<p><img src=\"/images/probot-04-github-deliveries.png\" alt=\"Github Recent Webhook Deliveries\"></p>\n<hr>\n<p>Both instances provided a button to redeliver the webhook event. And SMEE nicely provided a copy-to-clipboard button which would be very valuable is copying over a large payload.</p>\n<h3>SMEE Recent Webhook Expanded</h3>\n<p><img src=\"/images/probot-04-smee-deliveries-expanded.png\" alt=\"SMEE Recent Webhook Deliveries\"></p>\n<h3>Github Recent Webhook Expanded</h3>\n<p><img src=\"/images/probot-04-github-deliveries-expanded.png\" alt=\"Github Recent Webhook Deliveries\"></p>\n<h2>Using our Test Fixtures.</h2>\n<p>Thas's really all we did for this section. As you can see, both provide a very easy way to get test fixtures for any event we'd like to test. I went ahead and triggered all three events I was interested in. <code>installation.created</code>, <code>status</code> with a failed state, and <code>status</code> with a success state. Downloaded all three to json files in the test/fixtures folder and gave them appropriate names to match the payload, so its easy to recognize what's doing what.</p>\n<p>In the next section, we will use these fixtures to simulate and write some tests.</p>\n<div>\n  Excited yet? Let's do this! <span>ðŸŽ‰<span>\n</div>","frontmatter":{"title":"Testing Setup","description":"Let's setup our test and add some text fixtures","commit":null},"fields":{"slug":"/probot/en/test-fixture-setup/","chapter":"test-fixture-setup","framework":"probot","language":"en"}},"site":{"siteMetadata":{"title":"Probot Gitty Test","toc":["getting-started","create-probot-app","core-development","test-fixture-setup","writing-some-tests","deployment","conclusion"],"languages":["en"],"githubUrl":"https://github.com/nnennajohn/gitty-probot-test","codeGithubUrl":"https://github.com/nnennajohn/gitty-probot","siteUrl":"https://weekendprojects.io/"}},"pages":{"edges":[{"node":{"frontmatter":{"tocTitle":"Create Robot App","title":"Create Robot App","description":"Bootstrapping our App With Create Probot App"},"fields":{"slug":"/probot/en/create-probot-app/","chapter":"create-probot-app"}}},{"node":{"frontmatter":{"tocTitle":"Conclusion","title":"Conclusion","description":"And its a wrap"},"fields":{"slug":"/probot/en/conclusion/","chapter":"conclusion"}}},{"node":{"frontmatter":{"tocTitle":"Core Development","title":"Core Development","description":"Let's really get this party started!"},"fields":{"slug":"/probot/en/core-development/","chapter":"core-development"}}},{"node":{"frontmatter":{"tocTitle":"Testing Setup","title":"Testing Setup","description":"Let's setup our test and add some text fixtures"},"fields":{"slug":"/probot/en/test-fixture-setup/","chapter":"test-fixture-setup"}}},{"node":{"frontmatter":{"tocTitle":"Deployment","title":"Deployment","description":"Hello from the other side!"},"fields":{"slug":"/probot/en/deployment/","chapter":"deployment"}}},{"node":{"frontmatter":{"tocTitle":"Getting started","title":"Getting started","description":"Getting started by going through Probot Docs and Github Webhook API"},"fields":{"slug":"/probot/en/getting-started/","chapter":"getting-started"}}},{"node":{"frontmatter":{"tocTitle":"Writing Some Tests","title":"Writing Some Tests","description":"Testing the testing the testing!"},"fields":{"slug":"/probot/en/writing-some-tests/","chapter":"writing-some-tests"}}}]}},"pathContext":{"slug":"/probot/en/test-fixture-setup/","framework":"probot","language":"en","chapter":"test-fixture-setup"}}

